		getUserInfo
	} from '@/api/user.js';
	import {
		TOKENNAME,
		HTTP_REQUEST_URL
	} from '@/config/app.js';
	import colors from '@/mixins/color.js';
	import {Debounce} from '@/utils/validate.js'
	export default {
		components: {
			zbCode,
			productConSwiper,
			kefuIcon,
			// #ifdef MP
			authorize,
			// #endif
			"jyf-parser": parser,
			"product-window": productWindow,
			userEvaluation,
			countDown,
			storeLis,
			cusPreviewImg,
			homeList
		},
		computed: mapGetters({
			'isLogin': 'isLogin',
			'userData': 'userInfo'
		}),
		mixins:[colors],
		data() {
			return {
				showSkeleton: true, //骨架屏显示隐藏
				isNodes: 0, //控制什么时候开始抓取元素节点,只要数值改变就重新抓取
				dataShow: 0,
				navH: '',
				id: 0,
				userInfo: {},
				itemNew: [],
				indicatorDots: false,
				circular: true,
				autoplay: true,
				interval: 3000,
				duration: 500,
				attribute: {
					cartAttr: false,
					productAttr: [],
					productSelect: {}
				},
				productValue: [],
				isOpen: false,
				attr: '请选择',
				attrValue: '',
				AllIndex: 2,
				maxAllIndex: 0,
				replyChance: '',
				limitNum: 1,
				timeer: null,
				iSplus: false,
				navList: ['商品', '评价', '详情'],
				opacity: 0,
				scrollY: 0,
				topArr: [],
				toView: '',
				height: 0,
				heightArr: [],
				lock: false,
				scrollTop: 0,
				storeInfo: {
					brand_name:''
				},
				pink_ok_sum: 0,
				pink: [],
				replyCount: 0,
				reply: [],
				imgUrls: [],
				sharePacket: '',
				tagStyle: {
					img: 'width:100%;display:block;',
					table: 'width:100%',
					video: 'width:100%'
				},
				posters: false,
				weixinStatus: false,
				posterImageStatus: false,
				canvasStatus: false, //海报绘图标签
				storeImage: '', //海报产品图
				PromotionCode: '', //二维码图片
				posterImage: '', //海报路径
				posterbackgd: '/static/images/posterbackgd.png',
				navActive: 0,
				actionSheetHidden: false,
				attrTxt: '',
				cart_num: '',
				isAuto: false, //没有授权的不会自动授权
				isShowAuth: false, //是否隐藏授权
				AllIndexDefault: 0,
				homeTop: 20,
				returnShow: true,
				H5ShareBox: false,
				routineContact: 0,
				siteName:'', //商城名称
				themeColor:'',
				fontColor:'',
				skuArr:[],
				//二维码参数
				codeShow: false,
				cid: '1',
				codeVal: "", // 要生成的二维码值
				size: 200, // 二维码大小
				unit: 'upx', // 单位
				background: '#FFF', // 背景色
				foreground: '#000', // 前景色
				pdground: '#000', // 角标色
				codeIcon: '', // 二维码图标
				iconsize: 40, // 二维码图标大小
				lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
				onval: true, // val值变化时自动重新生成二维码
				loadMake: true, // 组件加载完成后自动生成二维码
				base64Show: 0,
				shareQrcode: 0,
				followCode:'',
				selectSku:{},
				currentPage:false,
				sysHeight: sysHeight,
				isMention: false, //是否存在到店自提
				storeLen:1,
				imgHost:HTTP_REQUEST_URL,
			}
		},
		watch: {
			isLogin: {
				handler: function(newV, oldV) {
					if (newV) {
						// this.downloadFilePromotionCode();
						this.combinationDetail();
					}
				},
				deep: true
			},
		},
		onLoad(options) {
			let that = this
			var pages = getCurrentPages();
			that.returnShow = pages.length === 1 ? false : true;
			this.$nextTick(() => {
				// #ifdef MP
				const menuButton = uni.getMenuButtonBoundingClientRect();
				const query = uni.createSelectorQuery().in(this);
				query
					.select('#home')
					.boundingClientRect(data => {
						this.homeTop = menuButton.top * 2 + menuButton.height - data.height;
					})
					.exec();
				// #endif
			})
			// #ifdef MP
			this.navH = app.globalData.navHeight;
			// #endif
			// #ifndef MP
			this.navH = 96;
			// #endif
			//设置商品列表高度
			uni.getSystemInfo({
				success: function(res) {
					that.height = res.windowHeight
					//res.windowHeight:获取整个窗口高度为px，*2为rpx；98为头部占据的高度；
				},
			});
			//扫码携带参数处理
			// #ifdef MP
			if (options.scene) {
				let value = this.$util.getUrlParams(decodeURIComponent(options.scene));
				if (value.id) options.id = value.id;
				//记录推广人uid
				if (value.pid) app.globalData.spid = value.pid;
			}
			if (!options.id && !options.scene) return this.$util.Tips({
				title: '缺少参数无法查看商品'
			}, {
				tab: 3,
				url: 1
			});
			// #endif
			if (options.hasOwnProperty('id')) {
				this.id = options.id
				//记录推广人uid
				if (options.pid) app.globalData.spid = options.pid;
				// app.globalData.openPages = '/pages/activity/goods_combination_details/index?id=' + this.id + '&spid=' + this.userInfo.uid;
				if (this.isLogin && options.id) {
					// #ifdef MP
					that.downloadFilePromotionCode();
					// #endif
					this.combinationDetail();
				} else {
					// #ifdef H5 || APP-PLUS
					try {
						uni.setStorageSync('comGoodsId', options.id);
					} catch (e) {}
					// #endif 
					this.$Cache.set('login_back_url',
						`/pages/activity/goods_combination_details/index?id=${options.id}`);
					toLogin();
				}
			} else {
				try {
					let val = uni.getStorageSync('comGoodsId');
					if (val != '') {
						this.id = val
						this.combinationDetail();
					}
				} catch (e) {
					uni.showToast({
						title: '参数错误',
						icon: 'none',
						duration: 1000,
						mask: true,
					})
				}
			};
			this.colorData();
			let color = [
				{themeColor:'#1DB0FC',fontColor:'#FD502F'},
				{themeColor:'#42CA4D',fontColor:'#FF7600'},
				{themeColor:'#e93323',fontColor:'#e93323'},
				{themeColor:'#FF448F',fontColor:'#FF448F'},
				{themeColor:'#FE5C2D',fontColor:'#FE5C2D'}
				];
			setTimeout(()=>{
				switch (this.colorNum) {
					case 1:
						this.themeColor = color[0].themeColor;
						this.fontColor = color[0].fontColor;
						break;
					case 2:
						this.themeColor = color[1].themeColor;
						this.fontColor = color[1].fontColor;
						break;
					case 3:
						this.themeColor = color[2].themeColor;
						this.fontColor = color[2].fontColor;
						break;
					case 4:
						this.themeColor = color[3].themeColor;
						this.fontColor = color[3].fontColor;
						break;
					case 5:
						this.themeColor = color[4].themeColor;
						this.fontColor = color[4].fontColor;
						break;
					default:
						this.themeColor = color[2].themeColor;
						this.fontColor = color[2].fontColor;
						break
				}
			},1)
		},
