				// #ifdef MP
				if(that.PromotionCode==''&&!that.isDown){
					return that.$util.Tips({
						title: '小程序二维码需要发布正式版后才能获取到'
					},function(){
						that.posters = false;
					});
				}
				// #endif
				////#ifndef H5
				if (that.isDown) return that.$util.Tips({
					title: '正在下载海报,请稍后再试！'
				},function(){
					that.posters = false;
				});
				//// #endif
				uni.getImageInfo({
					src: that.PromotionCode,
					fail: function(res) {
						return that.$util.Tips({
							title: '小程序二维码需要发布正式版后才能获取到'
						});
					},
					success() {
						if (arr2[2] == '') {
							//海报二维码不存在则从新下载
							that.downloadFilePromotionCode(function(msgPromotionCode) {
								arr2[2] = msgPromotionCode;
								if (arr2[2] == '')
									return that.$util.Tips({
										title: '海报二维码生成失败！'
									});
								that.$nextTick(function(){
									that.$util.PosterCanvas(that.fontColor, that.themeColor, that.siteName, arr2, that.storeInfo.title, that.storeInfo
										.price, that.storeInfo.product_price,
										function(tempFilePath) {
											that.$set(that, 'posterImage', tempFilePath);
											that.$set(that, 'posterImageStatus', true);
											that.$set(that, 'canvasStatus', false);
											that.$set(that, 'actionSheetHidden', !that
												.actionSheetHidden);
										});
								})
							});
						} else {
							//生成推广海报
							that.$nextTick(function(){
								that.$util.PosterCanvas(that.fontColor, that.themeColor, that.siteName, arr2, that.storeInfo.title, that.storeInfo.price, that
									.storeInfo.product_price,
									function(tempFilePath) {
										that.$set(that, 'posterImage', tempFilePath);
										that.$set(that, 'posterImageStatus', true);
										that.$set(that, 'canvasStatus', false);
										that.$set(that, 'actionSheetHidden', !that.actionSheetHidden);
									});
							})
						}
					},
				});
			},

			/*
			 * 保存到手机相册
			 */
			// #ifdef MP
			savePosterPath: function() {
				let that = this;
				uni.getSetting({
					success(res) {
						if (!res.authSetting['scope.writePhotosAlbum']) {
							uni.authorize({
								scope: 'scope.writePhotosAlbum',
								success() {
									uni.saveImageToPhotosAlbum({
										filePath: that.posterImage,
										success: function(res) {
											that.posterImageClose();
											that.$util.Tips({
												title: '保存成功',
												icon: 'success'
											});
										},
										fail: function(res) {
											that.$util.Tips({
												title: '保存失败'
											});
										}
									})
								}
							})
						} else {
							uni.saveImageToPhotosAlbum({
								filePath: that.posterImage,
								success: function(res) {
									that.posterImageClose();
									that.$util.Tips({
										title: '保存成功',
										icon: 'success'
									});
								},
								fail: function(res) {
									that.$util.Tips({
										title: '保存失败'
									});
								},
							})
						}
					}
				})
			},
			// #endif
			//#ifdef APP-PLUS
			savePosterPath() {
				let that = this
				uni.saveImageToPhotosAlbum({
					filePath: that.posterImage,
					success: function(res) {
						that.posterImageClose();
						that.$util.Tips({
							title: '保存成功',
							icon: 'success'
						});
					},
					fail: function(res) {
						that.$util.Tips({
							title: '保存失败'
						});
					}
				});
			},
			// #endif
			// setShareInfoStatus: function() {
			// 	let data = this.storeInfo;
			// 	let href = location.href;
			// 	if (this.$wechat.isWeixin()) {
			// 		getUserInfo().then(res => {
			// 			href =
			// 				href.indexOf("?") === -1 ?
			// 				href + "?spread=" + res.data.uid :
			// 				href + "&spread=" + res.data.uid;

			// 			let configAppMessage = {
			// 				desc: data.store_info,
			// 				title: data.store_name,
			// 				link: href,
			// 				imgUrl: data.image
			// 			};
			// 			this.$wechat.wechatEvevt(["updateAppMessageShareData", "updateTimelineShareData"],
			// 				configAppMessage)
			// 		});
			// 	}
			// },





			scroll: function(e) {
				var that = this,
					scrollY = e.detail.scrollTop;
				var opacity = scrollY / 200;
				opacity = opacity > 1 ? 1 : opacity;
				that.opacity = opacity
				that.scrollY = scrollY
				that.currentPage = false
				if (that.lock) {
					that.lock = false
					return;
				}
				for (var i = 0; i < that.topArr.length; i++) {
					if (scrollY < that.topArr[i] - (app.globalData.navHeight / 2) + that.heightArr[i]) {
						that.navActive = i
						break
					}
				}
			},
			tap: function(item, index) {
				var id = item.id;
				var index = index;
				var that = this;
				if (!this.replyCount && id == "past1") {
				  id = "past2"
				}
				this.toView = id;
				this.navActive = index;
				this.lock = true;
				this.scrollTop = index > 0 ? that.topArr[index] - (app.globalData.navHeight / 2) : that.topArr[index]
			},
		},
		//#ifdef MP
		onShareAppMessage() {
			return {
				title: this.storeInfo.title,
				path: app.globalData.openPages,
				imageUrl: this.storeInfo.image
			};
		},
		onShareTimeline() {
			let that = this;
			return {
				title: this.storeInfo.title,
				path: app.globalData.openPages,
				imageUrl: this.storeInfo.image
			};
		},
		//#endif

	}
</script>

